.Asso-container{
    display: grid;
    align-items: center;
}

.Asso-h4{
    font-family: "Poiret One", serif;
    font-weight: lighter;
    margin: 15px 0;
    color: #13435b;
}

.Asso-p{
    font-family: "Roboto", sans-serif;
    font-weight: normal;
    margin: 10px 0;
    text-indent: 30px;
    text-align: justify;
}

.AllerPlusLoin-container{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

@media only screen and (min-width: 768px) {
    .Asso-container{
        grid-gap: 50px;
        grid-template-columns: 200px auto;
        margin-bottom: 40px;
    }

    .VieAssociative-Assos-Logos{width: 200px;}
    .Asso-h4{font-size: 50px;}
    .Asso-p{font-size: 20px;}
    .AllerPlusLoin-icons{width: 30px; padding: 3px}
}

@media only screen and (max-width: 767px) {
    .Asso-container{
        grid-gap: 15px;
        grid-template-areas:
                "a"
                "b";
        margin-bottom: 40px;
    }

    .AllerPlusLoin-container{grid-gap: 4px;}
    .AssoText-container{grid-area: b}
    .VieAssociative-Assos-Logos{max-width: 100%; max-height: 150px; justify-self: center; grid-area: a}
    .Asso-h4{font-size: 30px; text-align: center}
    .Asso-p{font-size: 18px;}
    .AllerPlusLoin-icons{width: 20px; padding: 2px}
}