.VieAssociative-h3{
    font-family: "Poiret One", serif;
    font-weight: lighter;
    margin: 10px 20px;
    color: #13435b;
}

.VieAssociative-p{
    font-family: "Roboto", sans-serif;
    font-weight: normal;
    text-indent: 30px;
    text-align: justify;
}

.VieAssociative-titre{
    display: flex;
    align-items: center;
}

@media only screen and (min-width: 768px) {
    .VieAssociative-p{margin: 10px 0 -50px;}
    #VieAssociative-fleche{height: 200px;}
    .VieAssociative-titre{margin-left:-100px}
    .VieAssociative-texte{margin: 100px;}
    .VieAssociative-p{font-size: 20px;}
    .VieAssociative-h3{font-size: 70px;}
}

@media only screen and (max-width: 767px) {
    .VieAssociative-p{margin: 10px 0 -1px;}
    #VieAssociative-fleche{height: 75px;}
    .VieAssociative-titre{margin-left:-20px}
    .VieAssociative-texte{margin: 20px;}
    .VieAssociative-p{font-size: 18px;}
    .VieAssociative-h3{font-size: 30px;}
}