.Poles-h3{
    font-family: "Poiret One", serif;
    font-weight: lighter;
    color: #13435b;
    align-self: center;
}

.Poles-BTN {
    cursor: pointer;
    display: grid;
    background-color: #f2f2f2;
    grid-template-areas: "a b c";
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.5);
}

.Poles-LogosAssosMin-container{
    display: flex;
    align-items: center;
    align-self: center;
    justify-content: left;
}

#VDE_color{background-color: #6f80c9}
#BUSINESS_color{background-color: #8559b2}
#CITOYENNETE_color{background-color: #f789cd}
#CULTURE_color{background-color: #ed3e4b}
#INNO_color{background-color: #ef7f22}
#SPORT_color{background-color: #51bf5b}

@media only screen and (min-width: 768px) {
    .Poles-BTN {grid-template-areas: "a b c";}

    .Poles-container {
        margin-left: 100px;
        margin-right: 100px;
    }

    .Poles-BTN{
        grid-template-columns: 10px 350px auto;
        grid-template-rows: 70px;
        grid-gap: 15px;
        margin-bottom: 25px;

    }

    .Poles-h3{
        font-size: 40px;
        padding: 5px;
    }

    .LogosBTN{
        height: 30px;
        padding: 2px;
    }
}

@media only screen and (max-width: 767px) {
    .Poles-BTN {grid-template-areas:
            "a b"
            "a c";}

    #VDE_color{grid-area: a}
    #BUSINESS_color{grid-area: a}
    #CITOYENNETE_color{grid-area: a}
    #CULTURE_color{grid-area: a}
    #INNO_color{grid-area: a}
    #SPORT_color{grid-area: a}

    .Poles-LogosAssosMin-container{grid-area: c; margin: 3px}

    .Poles-container {
        margin-left: 20px;
        margin-right: 20px;
    }

    .Poles-h3{font-size: 25px; grid-area:b}

    .Poles-BTN{
        grid-template-columns: 5px auto;
        grid-template-rows: 45px;
        grid-gap: 5px;
        margin-bottom: 15px;
    }

    .LogosBTN{
        height: 15px;
        padding: 2px;
        margin-top: -12px;
    }
}